.innerGrid {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 1681px;
  height: 650px;
  background-color: #ECECEC;
  margin-left: -10px;

  padding: 32px;
}
.leftGrid{
  display: flex;
  flex-direction: row;
}
.leftDiv {
  display: flex;
  flex-direction: column;
}
.title {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  display: flex;
  align-items: center;

  color: #000000;

  margin-bottom: 10px;
}
.subTitle {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 28.8889px;
  line-height: 35px;
  /* identical to box height */
  display: flex;
  align-items: center;

  color: #000000;
  margin-bottom: 8px;
}
.text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  display: flex;
  align-items: center;

  color: #000000;
}
.rubrosDiv {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  height: 460px;
  margin-top: 10px;
  padding-right: 13px;
}
.separator {
  width: 0.5px;
  height: 100%;
  background-color: black;
  margin-left: 26px;
}
.buttonLeftDiv {
  display: flex;
  justify-content: end;
  width: 100%;
  margin-top: -30px;
  & button {
      margin-left: 10px;
      width: 107px;
      height: 33px;
    }
}
